<template>
  <div class="cart">
    <h4></h4>
    <!--  -->
    <div class="cart-main">
      <div class="cart-th">
        <div class="cart-th1"></div>
        <div class="cart-th2"></div>
        <div class="cart-th3"></div>
        <div class="cart-th4"></div>
        <div class="cart-th5"></div>
        <div class="cart-th6"></div>
      </div>
      <div class="cart-body">
        <ul class="cart-list" v-for="(cart, index) in cartInfoList" :key="cart.id">
          <li class="cart-list-con1">
            <input type="checkbox" name="chk_list" :checked="cart.isChecked == 1" @change="updateChecked(cart, $event)">
          </li>
          <li class="cart-list-con2">
            <img :src="cart.imgUrl">
            <div class="item-msg">MIJIA  1080p360AI</div>
          </li>
          <li class="cart-list-con4">
            <span class="price">{{ cart.skuPrice }}.00</span>
          </li>
          <li class="cart-list-con5">
            <a href="javascript:void(0)" class="mins" @click="handler('minus', -1, cart)">-</a>
            <input autocomplete="off" type="text" minnum="1" class="itxt" :value="cart.skuNum"
              @change="handler('change', $event.target.value * 1, cart)">
            <a href="javascript:void(0)" class="plus" @click="handler('add', 1, cart)">+</a>
          </li>
          <li class="cart-list-con6">
            <span class="sum">{{ cart.skuNum * cart.skuPrice }}</span>
          </li>
          <li class="cart-list-con7">
            <a class="sindelet" @click="deleteCartById(cart)"></a>
            <br>
            <a href="#none"></a>
          </li>
        </ul>
      </div>
    </div>
    <div class="cart-tool">
      <div class="select-all">
        <input class="chooseAll" type="checkbox" :checked="isAllCheck&&cartInfoList.length>0" @change="updateAllCartChecked">
        <span></span>
      </div>
      <div class="option">
        <a @click="deleteAllCheckedCart"></a>
        <a href="#none"></a>
        <a href="#none"></a>
      </div>
      <div class="money-box">
        <div class="chosed">
          <span>0</span>
        </div>
        <div class="sumprice">
          <em> </em>
          <i class="summoney">0</i>
        </div>
        <div class="sumbtn">
          <router-link class="sum-btn" to="/trade"></router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import throttle from 'lodash/throttle';
export default {
  name: 'ShopCart',
  mounted() {
    this.getData();
  },
  methods: {
    getData() {
      this.$store.dispatch('getCartList');
    },
    // , 
    // async handler: throttle(async function (type, disNum, cart) 
    handler: throttle(async function (type, disNum, cart) {
      // type: (+ - )
      // disNum: : + (1), - (-1), input()
      // cart: ( id)
      // : , , 
      switch (type) {
        //:
        case "add":
          disNum = 1;
          break;
        //
        case "minus":
          // ()  -1, 10 ()
          disNum = cart.skuNum > 1 ? -1 : 0;
          break;
        // 
        case "change":
          //: , , ,   0
          if (isNaN(disNum) || disNum < 1) {
            disNum = 0;
          } else {
            //  ()
            disNum = parseInt(disNum) - cart.skuNum;
          };
          break;
      }
      // action
      try {
        //
        await this.$store.dispatch('addOrUpdateShopCart', {
          // id
          skuId: cart.skuId,
          // 
          skuNum: disNum,
        });
        // 
        this.getData();
      } catch (error) {
        alert(error.message)
      }
    }, 1000),
    // 
    async deleteCartById(cart) {
      try {
        await this.$store.dispatch('deleteCartListBySkuId', cart.skuId);
        this.getData();
      } catch (error) {
        alert(error.message)
      }
    },
    // 
    async updateChecked(cart, event) {
      // 
      try {
        // isChecked, , 0|1
        let isChecked = event.target.checked ? '1' : '0';
        await this.$store.dispatch('updateCheckedById', { skuId: cart.skuId, isChecked });
        // 
        this.getData()
      } catch (error) {
        alert(error.message)
      }
    },
    // 
    async deleteAllCheckedCart() {
      try {
        // actions
        await this.$store.dispatch('deleteAllCheckedCart');
        // 
        this.getData();

      } catch (error) {
      }
    },
    // 
    async updateAllCartChecked(event) {
      try {
        // , 1 0
      let isChecked = event.target.checked? "1" : "0"
      // action
      await this.$store.dispatch('updateAllCartIsChecked', isChecked);
      this.getData();
      } catch (error) {
        alert(error.message)
      }
    }
  },
  computed: {
    ...mapGetters(['cartList']),
    //, 
    cartInfoList() {
      return this.cartList.cartInfoList || []
    },
    // 
    totalPrice() {
      let sum = 0
      // 
      this.cartInfoList.forEach(item => {
        sum += item.skuNum * item.skuPrice
      });
      return sum;
    },
    // 
    isAllCheck() {
      // every cartInfoList,  1 false
      return this.cartInfoList.every(item => item.isChecked == 1)
    }
  },
}
</script>

<style lang="less" scoped>
.cart {
  width: 1200px;
  margin: 0 auto;

  h4 {
    margin: 9px 0;
    font-size: 14px;
    line-height: 21px;
  }

  .cart-main {
    .cart-th {
      background: #f5f5f5;
      border: 1px solid #ddd;
      padding: 10px;
      overflow: hidden;

      &>div {
        float: left;
      }

      .cart-th1 {
        width: 25%;

        input {
          vertical-align: middle;
        }

        span {
          vertical-align: middle;
        }
      }

      .cart-th2 {
        width: 25%;
      }

      .cart-th3,
      .cart-th4,
      .cart-th5,
      .cart-th6 {
        width: 12.5%;

      }
    }

    .cart-body {
      margin: 15px 0;
      border: 1px solid #ddd;

      .cart-list {
        padding: 10px;
        border-bottom: 1px solid #ddd;
        overflow: hidden;

        &>li {
          float: left;
        }

        .cart-list-con1 {
          width: 15%;
        }

        .cart-list-con2 {
          width: 35%;

          img {
            width: 82px;
            height: 82px;
            float: left;
          }

          .item-msg {
            float: left;
            width: 150px;
            margin: 0 10px;
            line-height: 18px;
          }
        }

        .cart-list-con4 {
          width: 10%;

        }

        .cart-list-con5 {
          width: 17%;

          .mins {
            border: 1px solid #ddd;
            border-right: 0;
            float: left;
            color: #666;
            width: 6px;
            text-align: center;
            padding: 8px;
          }

          input {
            border: 1px solid #ddd;
            width: 40px;
            height: 33px;
            float: left;
            text-align: center;
            font-size: 14px;
          }

          .plus {
            border: 1px solid #ddd;
            border-left: 0;
            float: left;
            color: #666;
            width: 6px;
            text-align: center;
            padding: 8px;
          }
        }

        .cart-list-con6 {
          width: 10%;

          .sum {
            font-size: 16px;
          }
        }

        .cart-list-con7 {
          width: 13%;

          a {
            color: #666;
          }
        }
      }
    }
  }

  .cart-tool {
    overflow: hidden;
    border: 1px solid #ddd;

    .select-all {
      padding: 10px;
      overflow: hidden;
      float: left;

      span {
        vertical-align: middle;
      }

      input {
        vertical-align: middle;
      }
    }

    .option {
      padding: 10px;
      overflow: hidden;
      float: left;

      a {
        float: left;
        padding: 0 10px;
        color: #666;
      }
    }

    .money-box {
      float: right;

      .chosed {
        line-height: 26px;
        float: left;
        padding: 0 10px;
      }

      .sumprice {
        width: 200px;
        line-height: 22px;
        float: left;
        padding: 0 10px;

        .summoney {
          color: #c81623;
          font-size: 16px;
        }
      }

      .sumbtn {
        float: right;

        a {
          display: block;
          position: relative;
          width: 96px;
          height: 52px;
          line-height: 52px;
          color: #fff;
          text-align: center;
          font-size: 18px;
          font-family: "Microsoft YaHei";
          background: #e1251b;
          overflow: hidden;
        }
      }
    }
  }
}
</style>