<template>
  <div class="fr page">
    <div class="sui-pagination clearfix">
      <ul>
        <button :disabled="pageNo == 1" @click="$emit('getPageNo', pageNo - 1)"></button>
        <!-- ,  -->
        <!-- 1 -->
        <li><a v-if="startNumAndEndNum.start > 1" @click="$emit('getPageNo', 1)">1</a></li>
        <!-- 2 -->
        <li><span v-if="startNumAndEndNum.start > 2">...</span></li>

        <!--  1-->
        <li><a v-for="(page, index) in startNumAndEndNum.end" :key="index" v-if="page >= startNumAndEndNum.start"
            @click="$emit('getPageNo', page)" :class='{ active: pageNo == page }'>
            {{ page }}</a></li>
        <!-- 1 -->
        <li class="dotted"><span v-if="startNumAndEndNum.end < totalPage - 1">...</span></li>
        <!--  -->
        <li><a v-if="startNumAndEndNum.end < totalPage" @click="$emit('getPageNo', totalPage)">{{ totalPage }}</a></li>
        <button :disabled="pageNo == totalPage" @click="$emit('getPageNo', pageNo + 1)"></button>
        <div class="end"><span>{{ total }}</span></div>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Pagination',
  props: ['pageNo', 'pageSize', 'total', 'continues'],
  computed: {
    // 
    totalPage() {
      // Math.ceil: ( / )
      return Math.ceil(this.total / this.pageSize);
    },
    //(5)
    startNumAndEndNum() {
      // , this.continues this.xxx 
      const { continues, pageNo, totalPage } = this;
      // 
      let start = 0, end = 0;
      // 
      if (continues > totalPage) {
        start = 1;  //1
        end = totalPage;  //
      } else {
        // (5: , )
        start = pageNo - parseInt(continues / 2);  // =  -  / 2
        end = pageNo + parseInt(continues / 2);  // =  +  / 2 
        //1star, start()  -- 1(1)
        if (start < 1) {
          start = 1,  //start 1 0 | 
          end = continues;  //  
        }
        //, end() -- ()
        if (end > totalPage) {
          end = totalPage, //, 
          start = totalPage - continues + 1; //,,, , 
        }
      }
      return { start, end }
    }
  }
}
</script>

<style scoped lang="less">
.page {
  width: 1000px;
  height: 55px;
  margin: 0 auto;
  overflow: hidden;

  button {
    width: 60px;
    height: 38px;
    text-align: center;
    margin-top: -11px;
    // margin-right: 10px;
    border: 1px solid #e0e9ee;
    background-color: #fafafa;
  }

  .sui-pagination {
    width: 100%;
    margin: 18px 0;
    margin-left: 250px;
  }

  ul {
    display: flex;
    vertical-align: middle;
    width: 100%;
    text-align: center;

    li {
      margin: 0 10px;
    }

    .end {
      border: 1px solid #e0e9ee;
      margin-top: -11px;
      color: #333;
      width: 60px;
      height: 38px;
      font-size: 14px;
      line-height: 38px;
      margin-left: 10px;
    }
  }

  a {
    line-height: 18px;
    text-decoration: none;
    background-color: #fff;
    border: 1px solid #e0e9ee;
    margin: 0 5px;
    font-size: 14px;
    padding: 9px 18px;
    color: #333;
  }

  .active {
    background-color: #fff;
    color: #e1251b;
    border-color: rgb(236, 11, 11);
  }
}
</style>