<script>
export default {
  name: 'myOrder',
  data() {
    return {
      // 
      // 
      page: 1,
      // 
      limit: 3,
      // 
      myOrder: {},
    }
  },
  mounted() {
    // 
    this.getData();
  },
  methods: {
    // 
    async getData() {
      // 
      const {page, limit} = this;
      let result = await this.$API.reqMyOrderList(page, limit);
      if(result.code == 200) {
        this.myOrder = result.data;
      }
    },
    // 
    getPageNo(page) {
      // page
      this.page = page;
      this.getData();
    }
  }
}
</script>

