<template>
  <div>
    <TypeNav />
    <div class="main">
      <div class="py-container">
        <!--bread : x-->
        <div class="bread">
          <ul class="fl sui-breadcrumb">
            <li>
              <a href="#"></a>
            </li>
          </ul>
          <ul class="fl sui-tag">
            <!--  -->
            <li class="with-x" v-if="searchParams.categoryName">{{ searchParams.categoryName }}
              <i @click="removeCategoryName"></i>
            </li>
            <!--  -->
            <li class="with-x" v-if="searchParams.keyword">{{ searchParams.keyword }}
              <i @click="removeKeyword"></i>
            </li>
            <!--  -->
            <li class="with-x" v-if="searchParams.trademark">{{ searchParams.trademark.split(":")[1] }}
              <i @click="removeTrademark"></i>
            </li>
            <!--  -->
            <li class="with-x" v-for="(attrValue, index) in searchParams.props" :key="index">{{ attrValue.split(":")[1]
            }}
              <i @click="removeAttr(index)"></i>
            </li>
          </ul>
        </div>

        <!--selector-->
        <SearchSelector @trademarkInfo='trademarkInfo' @attrInfo="attrInfo" />

        <!--details -->
        <div class="details clearfix">
          <div class="sui-navbar">
            <div class="navbar-inner filter">
              <ul class="sui-nav">
                <li :class="{ active: isOne }" @click="changeOrder('1')">
                  <a><span v-show="isOne" class="iconfont"
                      :class="{ 'icon-upload': isAsc, 'icon-download': isDesc }"></span></a>
                </li>
                <li :class="{ active: isTwo }" @click="changeOrder('2')">
                  <a><span v-show="isTwo" class="iconfont"
                      :class="{ 'icon-upload': isAsc, 'icon-download': isDesc }"></span></a>
                </li>
              </ul>
            </div>
          </div>
          <!--  -->
          <div class="goods-list">
            <ul class="yui3-g">
              <li class="yui3-u-1-5" v-for="(goods, index) in goodsList" :key="goods.id">
                <div class="list-wrap">
                  <div class="p-img">
                    <!--  -->
                      <router-link :to="`/detail/${goods.id}`">
                        <img :src="goods.defaultImg" />
                      </router-link>
                  </div>
                  <div class="price">
                    <strong>
                      <em></em>
                      <i>{{ goods.price }}.00</i>
                    </strong>
                  </div>
                  <div class="attr">
                    <a target="_blank" href="item.html" title="CIBN4A 58 ">{{
                        goods.title
                    }}</a>
                  </div>
                  <div class="commit">
                    <i class="command"><span>2000</span></i>
                  </div>
                  <div class="operate">
                    <a href="success-cart.html" target="_blank" class="sui-btn btn-bordered btn-danger"></a>
                    <a href="javascript:void(0);" class="sui-btn btn-bordered"></a>
                  </div>
                </div>
              </li>
            </ul>
          </div>
          <!--  -->
          <!-- ,  -->
          <Pagination :pageNo="searchParams.pageNo" 
                      :pageSize="searchParams.pageSize" 
                      :total="total" :continues="5" 
                      @getPageNo="getPageNo"
          ></Pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SearchSelector from './SearchSelector/SearchSelector';
import { mapGetters, mapState } from 'vuex';
export default {
  name: 'search',
  components: {
    SearchSelector
  },
  data() {
    return {
      searchParams: {
        // 1, 2, 3, id, homesearch
        category1Id: "",
        category2Id: "",
        category3Id: "",
        // , 
        categoryName: "",
        keyword: "",
        // 
        order: "1:desc",
        // , 
        pageNo: 1,
        // 
        pageSize: 3,
        // 
        props: [],
        // 
        trademark: "",
      }
    }
  },
  beforeMount() {
    // 
    // this.searchParams.category1Id = this.$route.query.category1Id;
    // this.searchParams.category2Id = this.$route.query.category2Id;
    // this.searchParams.category3Id = this.$route.query.category3Id;
    // this.searchParams.categoryName = this.$route.query.categoryName;
    // this.searchParams.keyword = this.$route.query.keyword;
    Object.assign(this.searchParams, this.$route.query, this.$route.params);
  },
  mounted() {
    // // action
    // this.$store.dispatch('getSearchList', {});
    this.getData()
  },
  computed: {
    // mapGetters: , Getters(home, search)
    ...mapGetters(['goodsList']),
    isOne() {
      return this.searchParams.order.indexOf('1') != -1;
    },
    isTwo() {
      return this.searchParams.order.indexOf('2') != -1;
    },
    isAsc() {
      return this.searchParams.order.indexOf('asc') != -1;
    },
    isDesc() {
      return this.searchParams.order.indexOf('desc') != -1;
    },
    // search
    ...mapState({
      total: state => state.search.searchList.total
    })
  },
  methods: {
    // search,(,)
    // , ,
    getData() {
      this.$store.dispatch('getSearchList', this.searchParams);
    },
    // 
    removeCategoryName() {
      // (name, id), 
      // , ,  undefined()
      this.searchParams.categoryName = undefined;
      this.searchParams.category1Id = undefined;
      this.searchParams.category2Id = undefined;
      this.searchParams.category3Id = undefined;
      this.getData();
      // , (params, paramssearch, query)
      if (this.$route.params) {
        this.$router.push({ name: 'search', params: this.$route.params });
      }
    },
    // 
    removeKeyword() {
      // (keyword), 
      this.searchParams.keyword = undefined;
      this.getData();
      // 
      this.$bus.$emit('clear');
      // params
      if (this.$route.query) {
        // query
        this.$router.push({ name: 'search', query: this.$route.query })
      }
    },
    // : trademark
    trademarkInfo(trademark) {
      // , (id:)
      this.searchParams.trademark = `${trademark.tmId}:${trademark.tmName}`
      // 
      this.getData();
      console.log('', trademark);
    },
    // 
    removeTrademark() {
      // (tmId, tmName), 
      this.searchParams.trademark = undefined;
      this.getData();
    },
    //  : attr, attrValue
    attrInfo(attr, attrValue) {
      console.log(attr, attrValue);
      //  id: :   ()
      let props = `${attr.attrId}:${attrValue}:${attr.attrName}`;
      //  data props
      // (indexOf-1, , )
      if (this.searchParams.props.indexOf(props) == -1) {
        this.searchParams.props.push(props);
      }
      // 
      this.getData();
    },
    // 
    removeAttr(index) {
      console.log(index);
      //  (, )
      this.searchParams.props.splice(index, 1);
      // 
      this.getData();
    },
    // 
    // flag: , (1) (2) []
    changeOrder(flag) {
      // order(dataorder)originOrder
      let originOrder = this.searchParams.order;
      //  ((1)(2))
      // let originFlag = originOrder.split(':')[0];
      let originFlag = flag; // flag originOrder.split(':')[0] 
      //  (desc  asc)
      let originSort = originOrder.split(':')[1];
      //  ()
      let newOrder = '';
      //  (1(), 2)
      if(flag == originFlag) {
        // , originSort 
        newOrder = `${originFlag}:${originSort == 'desc'? 'asc': 'desc'}`
      }else {
        newOrder = `${flag}:${'desc'}`
      }
      this.searchParams.order = newOrder;
      this.getData();
    },
    // , 
    getPageNo(pageNo) {
      // console.log(pageNo);
      this.searchParams.pageNo = pageNo;
      this.getData();
    }
  },
  // :  
  watch: {
    // , , 
    $route(newValue, oldValue) {
      // 
      // , 1, 2, 3id, 
      this.searchParams.category1Id = '';
      this.searchParams.category2Id = '';
      this.searchParams.category3Id = '';
      Object.assign(this.searchParams, this.$route.query, this.$route.params);
      //  ajax
      this.getData()
      console.log(this.searchParams, this.$route.query, this.$route.params);
    },
  },
} 
</script>

<style lang="less" scoped>
.main {
  margin: 10px 0;

  .py-container {
    width: 1200px;
    margin: 0 auto;

    .bread {
      margin-bottom: 5px;
      overflow: hidden;

      .sui-breadcrumb {
        padding: 3px 15px;
        margin: 0;
        font-weight: 400;
        border-radius: 3px;
        float: left;

        li {
          display: inline-block;
          line-height: 18px;

          a {
            color: #666;
            text-decoration: none;

            &:hover {
              color: #4cb9fc;
            }
          }
        }
      }

      .sui-tag {
        margin-top: -5px;
        list-style: none;
        font-size: 0;
        line-height: 0;
        padding: 5px 0 0;
        margin-bottom: 18px;
        float: left;

        .with-x {
          font-size: 12px;
          margin: 0 5px 5px 0;
          display: inline-block;
          overflow: hidden;
          color: #000;
          background: #f7f7f7;
          padding: 0 7px;
          height: 20px;
          line-height: 20px;
          border: 1px solid #dedede;
          white-space: nowrap;
          transition: color 400ms;
          cursor: pointer;

          i {
            margin-left: 10px;
            cursor: pointer;
            font: 400 14px tahoma;
            display: inline-block;
            height: 100%;
            vertical-align: middle;
          }

          &:hover {
            color: #28a3ef;
          }
        }
      }
    }

    .details {
      margin-bottom: 5px;

      .sui-navbar {
        overflow: visible;
        margin-bottom: 0;

        .filter {
          min-height: 40px;
          padding-right: 20px;
          background: #fbfbfb;
          border: 1px solid #e2e2e2;
          padding-left: 0;
          border-radius: 0;
          box-shadow: 0 1px 4px rgba(0, 0, 0, 0.065);

          .sui-nav {
            position: relative;
            left: 0;
            display: block;
            float: left;
            margin: 0 10px 0 0;

            li {
              float: left;
              line-height: 18px;

              a {
                display: block;
                cursor: pointer;
                padding: 11px 15px;
                color: #777;
                text-decoration: none;
              }

              &.active {
                a {
                  background: #e1251b;
                  color: #fff;
                }
              }
            }
          }
        }
      }

      .goods-list {
        margin: 20px 0;

        ul {
          display: flex;
          flex-wrap: wrap;

          li {
            height: 100%;
            width: 20%;
            margin-top: 10px;
            line-height: 28px;

            .list-wrap {
              .p-img {
                padding-left: 15px;
                width: 215px;
                height: 255px;

                a {
                  color: #666;

                  img {
                    max-width: 100%;
                    height: auto;
                    vertical-align: middle;
                  }
                }
              }

              .price {
                padding-left: 15px;
                font-size: 18px;
                color: #c81623;

                strong {
                  font-weight: 700;

                  i {
                    margin-left: 5px;
                  }
                }
              }

              .attr {
                padding-left: 15px;
                width: 85%;
                overflow: hidden;
                margin-bottom: 8px;
                min-height: 38px;
                cursor: pointer;
                line-height: 1.8;
                display: -webkit-box;
                -webkit-box-orient: vertical;
                -webkit-line-clamp: 2;

                a {
                  color: #333;
                  text-decoration: none;
                }
              }

              .commit {
                padding-left: 15px;
                height: 22px;
                font-size: 13px;
                color: #a7a7a7;

                span {
                  font-weight: 700;
                  color: #646fb0;
                }
              }

              .operate {
                padding: 12px 15px;

                .sui-btn {
                  display: inline-block;
                  padding: 2px 14px;
                  box-sizing: border-box;
                  margin-bottom: 0;
                  font-size: 12px;
                  line-height: 18px;
                  text-align: center;
                  vertical-align: middle;
                  cursor: pointer;
                  border-radius: 0;
                  background-color: transparent;
                  margin-right: 15px;
                }

                .btn-bordered {
                  min-width: 85px;
                  background-color: transparent;
                  border: 1px solid #8c8c8c;
                  color: #8c8c8c;

                  &:hover {
                    border: 1px solid #666;
                    color: #fff !important;
                    background-color: #666;
                    text-decoration: none;
                  }
                }

                .btn-danger {
                  border: 1px solid #e1251b;
                  color: #e1251b;

                  &:hover {
                    border: 1px solid #e1251b;
                    background-color: #e1251b;
                    color: white !important;
                    text-decoration: none;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
</style>