// api 
// axios(, )
import requests from "./request";
// mock
import mockRequests from "./mockAjax"


// : /api/product/getBaseCategoryList get, 
export const reqCategoryList = ()=> {
  // : axios Promise
  return requests({url: '/product/getBaseCategoryList', method:'get'})
}

// banner(home)
export const reqGetBannerList = () => {
  return mockRequests.get('/banner');
}

// floor
export const reqGetFloorList = () => {
  return mockRequests.get('/floor');
}

// search: :/api/list, :post, 
// search, ()
export const reqGetSearchInfo = (params) => {
  return requests({url: '/list', method:'post', data:params});
}

//  URL: /api/item/{skuId} : get
export const reqGoodsInfo = (skuId) => {
  return requests({url:`/item/${skuId}`,method:'get'});
}

//  URL: api/cart/addToCart/{skuId}/{skuNum} post : skuId(id) skuNum()
export const reqAddOrUpdateShopCart = (skuId, skuNum) => {
  return requests({url:`/cart/addToCart/${skuId}/${skuNum}`, method:'post'})
}

//   URL: api/cart/cartList  get 
export const reqCartList = () => {
  return requests({url:`/cart/cartList`, method:'get'})
}

//  URL: api//cart/deleteCart/{skuId} : delete
export const reqDeleteCartById = (skuId) => {
  return requests({url:`/cart/deleteCart/${skuId}`, method:'delete'})
}

//  URL:/api/cart/checkCart/{skuId}/{isChecked} :get
export const reqUpdateCheckedById = (skuId, isChecked) => {
  return requests({url:`/cart/checkCart/${skuId}/${isChecked}`, method: 'get'})
}

//  URL: /api/user/passport/sendCode/{phone}  : get
export const reqGetCode = (phone) => {
  return requests({url:`/user/passport/sendCode/${phone}`, method: 'get'})
}

//  URL: /api/user/passport/register : post : phone, code, password
export const reqUserRegister = (data) => {
  return requests({url:`/user/passport/register`, data, method:'post'});
}

//  URL: /api/user/passport/login : post : phone password
export const reqUserLogin = (data) => {
  return requests({url:`/user/passport/login`, data, method:'post'});
}

//  URL:/api/user/passport/auth/getUserInfo  :get : 
export const reqUserInfo = () => {
  return requests({url:`/user/passport/auth/getUserInfo`, method:`get`});
}

//  URL: /api/user/passport/logout get
export const reqLogout = () => {
  return requests({url:`/user/passport/logout`, method:'get'});
}

//  URL: /api/user/userAddress/auth/findUserAddressList : get
export const reqAddressInfo = () => {
  return requests({url:`/user/userAddress/auth/findUserAddressList`, method: 'get'});
}

//  URL: /api/order/auth/trade :get
export const reqOrderInfo = () => {
  return requests({url:`/order/auth/trade`, method:'get'});
}

//  URL: /api/order/auth/submitOrder?tradeNo={tradeNo}  : post
export const reqSubmitOrder = (tradeNo, data) => {
  return requests({url:`/order/auth/submitOrder?tradeNo=${tradeNo}`, data, method: 'post'});
}

//  URL: /api/payment/weixin/createNative/{orderId} : get
export const reqPayInfo = (orderId) => requests({url:`/payment/weixin/createNative/${orderId}`, method: 'get'});

//  URL: /api/payment/weixin/queryPayStatus/{orderId} : get
export const reqPayStatus = (orderId) => requests({url:`/payment/weixin/queryPayStatus/${orderId}`, method: 'get'})

//  URL: /api/order/auth/{page}/{limit} : get
export const reqMyOrderList = (page, limit) => requests({url:`/order/auth/${page}/${limit}`, method: 'get'})